import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsPsDropdownAccountDeactivationnExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-reasons/ps-dropdown-reasons.component.interface';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsActionImage, IOptionsPsContainerPanel, IOptionsPsKeyinTextarea, IOptionsPsLabel, IOptionsPsTemplateForm } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'app-account-deletion',
  templateUrl: './account-deletion.page.html',
  styleUrls: ['./account-deletion.page.scss'],
})
export class AccountDeletionPage extends OmniBasePage implements OnInit {

  accountDeletionForm: FormGroup = new FormGroup({});
  defaultVO: any = {};
  istoggleClickedfcName: String = null;
  public parameters;
  othersChoosed = false;
  istglDelAcClicked = false;
  istglDelAcDataClicked = false;

  selectReasonDropDwnOptions: IOptionsPsDropdownAccountDeactivationnExposed = {
    labelKey: 'deletion_reason_key',
    placeHolder: 'select_a_deletion_reason_key',
    fcName: 'accountDeletionReason',
    group: this.accountDeletionForm,
    reasonType: '2'
  };
  inputOptions: IOptionsPsKeyinTextarea = {
    labelKey: 'comments_key',
    rows: '3',
    cols: '10',
    fcName: 'accountDeletionReasonDetail',
    group: this.accountDeletionForm
  };
  actionImageOptions: IOptionsPsActionImage = {
    imageName: 'account_Deactivation_Remark_icon.png',
  };
  warningMsgLabelOptions: IOptionsPsLabel = {
    labelKey: 'deletion_account_subsc_msg_key'
  };

  public panelAccountDeletionOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'account_deletion_key',
    expanded: true
  };

  public options: IOptionsPsTemplateForm = {
    group: this.accountDeletionForm,
    requestObject: this.defaultVO,
    submitOptions: {
      extraParams: {},
      group: this.accountDeletionForm,
      // check the accountDeletion
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.accountDeletion,

      preCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            this.executionClass.deletionConfirmation().then(() => {
              resolve(true);
            }).catch(() => {
              reject()
            })
          });
        },
        params: [this],
        executionClass: this

      },
      postCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.logOut(response));
          });
        },
        executionClass: this,
        params: [this]
      },
    }


  };

  // salah added main options for toggle
  public mainOptions = {
    group: this.accountDeletionForm,
    toggleDeleteAccountAndDeviceOptions: {
      labelKey: 'delete_account_and_all_data',
      placeHolder: 'delete_account_and_all_data',
      group: this.accountDeletionForm,
      fcName: 'deleteAccAndAllData',
      psClass: 'toggleColor',
    }
  };

  // changed from deactivationReasonValue
  deletionReasonValue: string = null;
  otherReasonValue: string = null;
  constructor(public commonProv: PsCommonService, public loggerP: LoggerService, private omniPull: OmniPullService, private navService: PsNavigatorService) {
    super();
  }

  onChangeReason(values: any) {

  }
  // change the ACCOUNT_DEACTIVATION_OPER
  ngOnInit() {
    super.init();
    this.baseFormGroup = this.accountDeletionForm;
  }

  ionViewDidEnter() {
    super.ionViewDidEnter();
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [
      this.selectReasonDropDwnOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [
      this.mainOptions.toggleDeleteAccountAndDeviceOptions.fcName], 0);
  }
  // changed the message from are_you_sure_?_key to proceed_with_account_deletion_key
  deletionConfirmation() {
    return new Promise<any>(async (resolve, reject) => {
      CommonUtils.presentInfoAlert(this.commonProv.translate('proceed_with_account_deletion_key'), {
        buttonsArray: [{
          group: this.accountDeletionForm,
          labelKey: 'ok_key',
          handler: () => {
            // this.options.submitOptions.extraParams = {
            //   fcname: this.istoggleClickedfcName
            // }
            this.commonProv.presentLoading();
            CommonUtils.dismissAllModals().then(res => {
              if (res) {
                resolve(true);

              }

            });
          }
        }, {
          group: this.options.group,
          type: 'reset',
          labelKey: 'cancel_key',
          handler: () => {
            CommonUtils.dismissAllModals();
            reject()
          }
        },]
      });
    });
  }
  // change the logout deactivationResponse
  logOut(deactivationResponse) {
    CommonUtils.presentInfoAlert(deactivationResponse.outputNotification, {
      buttonsArray: [{
        group: this.accountDeletionForm,
        labelKey: 'ok_key',
        handler: () => {
          this.commonProv.presentLoading();
          CommonUtils.dismissAllModals().then(res => {
            if (res) {
              this.commonProv.session.append(CommonBussinessConstant.REMEMBER_ME + PsCommonSettings.APP_ID, false, true);
              this.omniPull.omniCommon.logout(3).then(() => {
                this.commonProv.dismissLoading();
              });
            }
          });
        }
      }]
    });
    //  CommonUtils.presentSuccessAlert(loginResponse.result.outputNotification, { autoHide: true });

  }

  ontoggleChangeDelAccAndData(event) {
    if (event.newValue == true) {
      if (this.accountDeletionForm.get('deleteAccAndAllData').value === true) {
        this.commonProv.setValInsideNestedObj(this.mainOptions.toggleDeleteAccountAndDeviceOptions.fcName, true, this.defaultVO)
      }
    }

  }
}
